export const date =  time=>{

  let d = new Date();
  d.setTime(time);
  let year = d.getFullYear();
//  let year = d.getFullYear();
  // let year = d.getFullYear();
  // let year = d.getFullYear();
  // let year = d.getFullYear();
  // let year = d.getFullYear();
  let month  =  d.getMonth() + 1;  
               let day    =  d.getDate(); 
               let hour   =  d.getHours()

  return '时间：'+year+'年'+month+'月'+day +'日'+hour +'时'
}