<template>
  <div class="echart">

      <mt-header title="Echarts">
  <router-link to=" " slot="left">
    <mt-button icon="back" @click="goback">返回</mt-button>
  </router-link>
  <mt-button icon="more" slot="right"></mt-button>
</mt-header>
    <br/>
    <br/>
     <br/>
    <div id="echarts" style="height:300px;width:100%"></div>
    <!-- <div id="echarts2" style="height:300px;width:100%"></div> -->
  </div>
</template>

<script>
//  import echarts from "echarts";
var echarts = require('echarts/lib/echarts');
require("echarts/lib/chart/pie");

require('echarts/lib/component/tooltip');
require('echarts/lib/component/title');
export default {
    mounted(){
    var myChart = echarts.init(document.getElementById('echarts'));
     myChart.setOption({
    backgroundColor: '#2c343c',

    title: {
        text: '市场调查与搜索',
        left: 'center',
        top: 20,
        textStyle: {
            color: '#ccc'
        }
    },

    tooltip : {
        trigger: 'item',
        formatter: "{a} <br/>{b} : {c} ({d}%)"
    },

    visualMap: {
        show: false,
        min: 80,
        max: 600,
        inRange: {
            colorLightness: [0, 1]
        }
    },
    series : [
        {
            name:'访问来源',
            type:'pie',
            radius : '55%',
            center: ['50%', '50%'],
            data:[
                {value:335, name:'热点新闻'},
                {value:310, name:'娱乐大发现'},
                {value:274, name:'图片'},
                {value:235, name:'视频'},
                {value:400, name:'微博'}
            ].sort(function (a, b) { return a.value - b.value; }),
            roseType: 'radius',
            label: {
                normal: {
                    textStyle: {
                        color: 'rgba(255, 255, 255, 0.3)'
                    }
                }
            },
            labelLine: {
                normal: {
                    lineStyle: {
                        color: 'rgba(255, 255, 255, 0.3)'
                    },
                    smooth: 0.2,
                    length: 10,
                    length2: 20
                }
            },
            itemStyle: {
                normal: {
                    color: '#c23531',
                    shadowBlur: 200,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
            },
            animationType: 'scale',
            animationEasing: 'elasticOut',
            animationDelay: function (idx) {
                return Math.random() * 200;
            }
        }
    ]
});

    myChart.on("click", params => {
      console.log("click", params.name);
      window.open(
        "https://www.baidu.com/s?wd=" + encodeURIComponent(params.name)
      );
    });
   
},
 methods: {
          goback(){
      this.$router.go(-1)
    }
    }
}
</script>