import Vue from 'vue'
import VueRouter from 'vue-router';
Vue.use(VueRouter);

import Home from '../pages/Home.vue'
// import Follow from '../pages/Follow.vue'
let Follow = () =>import(/*webpackChunkName:"yuyu" */'../pages/Follow.vue')
// import Column from '../pages/Column.vue'
import User from '../pages/User.vue'
import Login from '../pages/Login.vue'
// import Reg from '../pages/Reg.vue'
let Reg=  () =>import(/*webpackChunkName:"yuyu" */'../pages/Reg.vue')
import Detail from '../pages/Detail.vue'
import Error from '../pages/Error.vue'
import Memberc from '../pages/Memberc.vue'
import BdMap from '../pages/BdMap.vue'
import Echarts from '../pages/Echarts.vue'
import BuyCar from '../pages/BuyCar.vue'

let routes=[
  {path:'/home',component:Home},
  {path:'/Follow',component:Follow},
  // {path:'/column',component:Column},
  {path:'/column',component:r=>require(['../pages/Column.vue'],r)},
  {path:'/User',component:User},
  {path:'/Login',component:Login},
  {path:'/Reg',component:Reg},
  {path:'/Memberc',component:Memberc},
  {path:'/bdmap',component:BdMap},
  {path:'/echarts',component:Echarts},
  // {path:'/Detail/:id',component:Detail,name:'detail'},
  {path:'/Detail/:id',component:Detail,name:'detail',props:(route)=>({id:route.params.id,...route.query})},
  {path:'/',redirect:'/home'},
  {path:'*',component:Error},
  {path:'/buycar',component:BuyCar},
];
export default new VueRouter({routes,	scrollBehavior(to,from,savedPosition){
  return { x: 0, y: 0 }},
  linkActiveClass: 'mui-active'//覆盖默认的路由高亮的类 默认的类叫做 router-link-active
 
  
 
//对于所有路由导航，简单地让页面滚动到顶部
	}

  
  )