<template>
  <div id="app" class="box">
    <Loading v-show="bLoading">
    </Loading>  
    <Header v-show="bNav"></Header>
     <!-- <transition
			enter-active-class = "animated fadeInLeft"
    > -->
    <keep-alive   :exclude="['Column']">
    <router-view></router-view>
    </keep-alive>
     <!-- </transition> -->
    <Footer v-show="bFoot"></Footer>
  </div>
</template>


<script>




import Header from './components/Header'
import Footer from './components/Footer'
import Loading  from './components/Loading'
import{mapState,mapMutations}from 'vuex'
import { VIEW_NAV,VIEW_FOOT } from './store/types.js';
export default{
  name:'app',
  components:{
    Header,Footer,Loading
  },
  methods:{
    checkPath(path){//路由检测 
    console.log('path',path);
    if(/home|follow|column/.test(path)){
      this.$store.dispatch(VIEW_NAV,true);
     this.$store.dispatch(VIEW_FOOT,true);

    } 
    if(/user|Memberc|bdmap/.test(path)){
      this.$store.dispatch(VIEW_NAV,false);
     this.$store.dispatch(VIEW_FOOT,true);
    }
    if(/login|reg|detail/.test(path)){
    this.$store.dispatch(VIEW_NAV,false);
     this.$store.dispatch(VIEW_FOOT,false);
    }
    }
  },
  watch:{
    $route:{
      handler(current,prev){
        // current.path==/home|/follow|/colukn|/detail/23423234
        this.checkPath(current.path)
      },
      immediate:true,  deep:true
    }
  },
  // mouted(){

  // }
computed:mapState(['bNav','bFoot','bLoading'])

}



</script>

